.user-block {

	display: flex;
	align-items: center;

	&__icon {
		width: 36px;
		height: 36px;

		padding: 11px;
		margin-right: 12px;

		background-color: #edeff1;
		border-radius: 50%;

		font-size: 14px;

		.icon-home {
			fill: #8a8f92; }

		&--small {
			width: 15px;
			height: 15px;

			padding: 0;
			margin-right: 6px;

			background-color: transparent;
			border-radius: none; } }


	&__name {
		margin-right: 12px;

		color: #2E3236;

		.icon-home {
			fill: #878c8f; }

		&--upper {
			text-transform: uppercase; } }

	&__name:hover {
		text-decoration: underline; }

	&__num {
		padding: 2px 6px;
		font-size: 10px;
		color: white;
		background-color: orange;
		border-radius: 50%; } }

