.nav {

	display: flex;
	justify-content: space-between;

	padding-right: 20px;

	&__toggler {
		display: none;
		position: absolute;
		top: 67px;
		left: 20px;

		font-size: 30px;
		background-color: transparent; }

	&__list {
		display: flex;
		align-items: center; }

	&__item {
		display: flex;
		align-items: center; }

	&__item:hover {
		background-color: #e2e2e2; }

	&__item-icon {
		width: 12px;
		height: 12px;

		margin-left: 20px;

		.icon {
			fill: #cccccc; } }

	&__link {
		color: #2e3236;
		text-transform: uppercase;
		padding: 15px 20px 15px 8px; } }

.is-active .nav {

	&__item-icon {
		display: none; }

	&__link {
		padding: 15px 30px;
		color: #ffffff; } }

.is-active.nav__item {

	background-color: #383e42; }


