.coments {

	position: relative;

	background-color: #edeff1;

	&__item {
		padding: 20px 25px; }

	&__header {
		display: flex;
		align-items: center;

		margin-bottom: 35px; }

	&__title {
		flex-grow: 1; }

	&__foto {
		margin-right: 10px; }

	&__descr {
		color: #024d8d; }

	&__text {
		font-size: 16px;
		line-height: 20px;
		color: #60686f;
		padding: 0 50px 0 10px;

		margin-bottom: 40px; }

	&__meta {
		display: flex;
		align-items: center;

		font-size: 13px;
		color: #60686F; }

	&__link {
		margin-right: 10px; }

	&__next,
	&__prev {
		position: absolute;
		bottom: 20px;

		width: 30px;
		height: 30px;

		cursor: pointer;
		border-radius: 50%;
		background-color: #ffffff;

		z-index: 5; }

	&__next img,
	&__prev img {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%); }

	&__next {
		right: 20px;

		padding-left: 5px; }

	&__prev {
		right: 60px; } }

