@include r(1230) {

	.container {
		width: 100%; }

	.search {

		&__input {
			width: 200px; } }

	.content-block {

		&__title-btn {
			top: 43px;
			right: 15px; } } }

@include r(1024) {

	.search {

		&__input {
			width: 100px; } }

	.options {
		margin-right: 30px; }

	.web-resources {

		&__item {
			padding: 18px 14px; } }

	.content-block {

		&__title-btn {
			top: 45px;
			right: 2px; } } }

@include r(768) {

	.container {
		width: 320px; }

	.header {

		&__top,
		&__nav {
			padding: 12px; }

		&__nav.active {
			margin-bottom: 220px; } }


	.logo {
		height: 20px;
		margin-right: 15px;

		& img {
			width: 120px; } }

	.search {

	 	&__input {
	 		display: none; } }

	.options {
		display: none; }

	.user-block {
		&__name {

			&--hide {
				display: none; } } }

	.nav {

		justify-content: flex-end;
		padding-right: 0;

		&__toggler {
			display: block; }

		&__list {
			display: none;

			position: absolute;
			top: 105px;

			width: 296px;

			flex-direction: column; }

		&__item {
			justify-content: center; } }

	.nav__list.show {
		display: block; }


	.main {

		&__layout {
			flex-direction: column; }

		&__item {
 			width: 320px; } }

	.coments {

		&__item {
			width: 320px; } }

	.support {

		&__list {
			width: auto;
			flex-direction: column; }

		&__item {
			width: auto; } } }


