.content-block {

	&--border-right {
		border-right: 1px solid #cccccc; }

	position: relative;
	padding: 25px 25px;

	&--no-padding {
		padding: 0; }

	&__icon {
		position: absolute;
		top: 15px;
		right: 25px;

		width: 40px;
		height: 40px;

		background-repeat: no-repeat;
		background-position: top right;

		&--left {
			left: 25px;
			top: 20px;
			background-position: top left; } }


	&__h2 {
		margin-bottom: 2px;
		font-size: 20px;
		font-weight: 400;
		color: #383e42;
		text-transform: uppercase; }

	&__h3 {
		margin-bottom: 5px;

		font-size: 17px;
		font-weight: 400;
		text-transform: capitalize;

		& a {
			color: #024d8d; }

		&.is-active a {
			color: #692b9e; } }


	&__descr {
		font-size: 13px;
		font-weight: 300; }

	&__title {
		margin-bottom: 30px;

		&--bg-grey {
			padding: 20px 25px 20px 65px;
			margin-bottom: 0;

			color: #ffffff;
			background-color: #746c6c; }

		&--bg-grey h2 {
			color: #ffffff; } }


	&__title-btn {
		position: absolute;
		top: 25px;
		right: 25px;

		font-size: 13px;
		color: #ffffff; }


	&__content {
		 margin-bottom: 20px; }

	&__summary p {
		 margin-bottom: 10px;
		 padding-right: 50px; }

	&__indication {
		display: flex;
		align-items: center;
		margin-bottom: 16px;

		font-size: 16px; }

	&__indication-icon {
		margin-right: 8px; } }

