html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrowLeft {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-arrowRight {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-burger {
  width: 1em;
  height: 1em;
  fill: #ccc;
}

.icon-card {
  width: 1em;
  height: 1em;
  fill: #010101;
}

.icon-document {
  width: 1em;
  height: 1em;
  fill: #000;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-home {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-options {
  width: 1em;
  height: 1em;
  fill: #515151;
}

.icon-search {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-star {
  width: 1em;
  height: 1em;
  fill: #333;
}

.icon-tasks {
  width: 1em;
  height: 1em;
  fill: none;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.container {
  width: 1230px;
  margin: 0 auto;
}

.out {
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
}

a {
  text-decoration: none;
}

.main__layout {
  display: flex;
}

.main__item {
  width: 33.333%;
}

.header__top {
  display: flex;
  align-items: center;
  padding: 20px 20px 15px 40px;
}

.header__nav {
  border-top: 1px solid #cccccc;
  border-bottom: 1px solid #cccccc;
}

.nav {
  display: flex;
  justify-content: space-between;
  padding-right: 20px;
}

.nav__toggler {
  display: none;
  position: absolute;
  top: 67px;
  left: 20px;
  font-size: 30px;
  background-color: transparent;
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item {
  display: flex;
  align-items: center;
}

.nav__item:hover {
  background-color: #e2e2e2;
}

.nav__item-icon {
  width: 12px;
  height: 12px;
  margin-left: 20px;
}

.nav__item-icon .icon {
  fill: #cccccc;
}

.nav__link {
  color: #2e3236;
  text-transform: uppercase;
  padding: 15px 20px 15px 8px;
}

.is-active .nav__item-icon {
  display: none;
}

.is-active .nav__link {
  padding: 15px 30px;
  color: #ffffff;
}

.is-active.nav__item {
  background-color: #383e42;
}

.search {
  display: flex;
  margin-right: 13px;
}

.search__input {
  width: 410px;
  height: 30px;
  padding: 0 15px;
  border: 1px solid #cccccc;
}

.search__btn {
  width: 30px;
  height: 30px;
  padding: 0;
  margin: 0 auto;
  margin-left: -1px;
  font-size: 14px;
  line-height: 30px;
  background-color: transparent;
  border: 1px solid #cccccc;
}

.search__btn .icon-search {
  fill: #cccccc;
}

.options {
  display: flex;
  align-items: center;
  height: 30px;
  margin-right: 50px;
}

.options__icon {
  width: 12px;
  height: 12px;
  margin-right: 10px;
}

.options__title {
  color: #024D8D;
}

.user-block {
  display: flex;
  align-items: center;
}

.user-block__icon {
  width: 36px;
  height: 36px;
  padding: 11px;
  margin-right: 12px;
  background-color: #edeff1;
  border-radius: 50%;
  font-size: 14px;
}

.user-block__icon .icon-home {
  fill: #8a8f92;
}

.user-block__icon--small {
  width: 15px;
  height: 15px;
  padding: 0;
  margin-right: 6px;
  background-color: transparent;
  border-radius: none;
}

.user-block__name {
  margin-right: 12px;
  color: #2E3236;
}

.user-block__name .icon-home {
  fill: #878c8f;
}

.user-block__name--upper {
  text-transform: uppercase;
}

.user-block__name:hover {
  text-decoration: underline;
}

.user-block__num {
  padding: 2px 6px;
  font-size: 10px;
  color: white;
  background-color: orange;
  border-radius: 50%;
}

.btn {
  display: inline-block;
  font-size: 10px;
  line-height: 12px;
  color: #2e3236;
  padding: 5px 15px;
  background-color: transparent;
  border: 1px solid #cccccc;
  border-radius: 70px;
}

.btn:hover {
  background-color: #edeff1;
  color: #2e3236;
}

.logo {
  height: 35px;
  margin-right: 25px;
}

.content-block {
  position: relative;
  padding: 25px 25px;
}

.content-block--border-right {
  border-right: 1px solid #cccccc;
}

.content-block--no-padding {
  padding: 0;
}

.content-block__icon {
  position: absolute;
  top: 15px;
  right: 25px;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-position: top right;
}

.content-block__icon--left {
  left: 25px;
  top: 20px;
  background-position: top left;
}

.content-block__h2 {
  margin-bottom: 2px;
  font-size: 20px;
  font-weight: 400;
  color: #383e42;
  text-transform: uppercase;
}

.content-block__h3 {
  margin-bottom: 5px;
  font-size: 17px;
  font-weight: 400;
  text-transform: capitalize;
}

.content-block__h3 a {
  color: #024d8d;
}

.content-block__h3.is-active a {
  color: #692b9e;
}

.content-block__descr {
  font-size: 13px;
  font-weight: 300;
}

.content-block__title {
  margin-bottom: 30px;
}

.content-block__title--bg-grey {
  padding: 20px 25px 20px 65px;
  margin-bottom: 0;
  color: #ffffff;
  background-color: #746c6c;
}

.content-block__title--bg-grey h2 {
  color: #ffffff;
}

.content-block__title-btn {
  position: absolute;
  top: 25px;
  right: 25px;
  font-size: 13px;
  color: #ffffff;
}

.content-block__content {
  margin-bottom: 20px;
}

.content-block__summary p {
  margin-bottom: 10px;
  padding-right: 50px;
}

.content-block__indication {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  font-size: 16px;
}

.content-block__indication-icon {
  margin-right: 8px;
}

.coments {
  position: relative;
  background-color: #edeff1;
}

.coments__item {
  padding: 20px 25px;
}

.coments__header {
  display: flex;
  align-items: center;
  margin-bottom: 35px;
}

.coments__title {
  flex-grow: 1;
}

.coments__foto {
  margin-right: 10px;
}

.coments__descr {
  color: #024d8d;
}

.coments__text {
  font-size: 16px;
  line-height: 20px;
  color: #60686f;
  padding: 0 50px 0 10px;
  margin-bottom: 40px;
}

.coments__meta {
  display: flex;
  align-items: center;
  font-size: 13px;
  color: #60686F;
}

.coments__link {
  margin-right: 10px;
}

.coments__next, .coments__prev {
  position: absolute;
  bottom: 20px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  border-radius: 50%;
  background-color: #ffffff;
  z-index: 5;
}

.coments__next img,
.coments__prev img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.coments__next {
  right: 20px;
  padding-left: 5px;
}

.coments__prev {
  right: 60px;
}

.social-btn {
  display: block;
  width: 24px;
  height: 24px;
  font-size: 0;
  cursor: pointer;
}

.social-btn--tw {
  background: url("../img/twitter.svg") no-repeat center center;
  background-size: cover;
}

.web-resources {
  display: flex;
  margin-top: 35px;
}

.web-resources__item {
  height: 110px;
  padding: 18px;
  margin-right: 20px;
  text-align: center;
  border: 1px solid #cccccc;
}

.web-resources__title {
  font-size: 17px;
  font-weight: 400;
  color: #383e42;
  margin-bottom: 35px;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 20px 25px;
  border-top: 1px solid #cccccc;
}

.copyright {
  text-align: end;
  font-size: 11px;
  color: #2e3236;
  opacity: 0.3;
}

.support__list {
  display: flex;
  flex-wrap: wrap;
  width: 450px;
}

.support__item {
  width: 145px;
  font-size: 11px;
}

.support__link {
  font-size: 11px;
  line-height: 20px;
  font-weight: 300;
  color: #2e3236;
  opacity: 0.5;
}

@media only screen and (max-width: 1230px) {
  .container {
    width: 100%;
  }
  .search__input {
    width: 200px;
  }
  .content-block__title-btn {
    top: 43px;
    right: 15px;
  }
}

@media only screen and (max-width: 1024px) {
  .search__input {
    width: 100px;
  }
  .options {
    margin-right: 30px;
  }
  .web-resources__item {
    padding: 18px 14px;
  }
  .content-block__title-btn {
    top: 45px;
    right: 2px;
  }
}

@media only screen and (max-width: 768px) {
  .container {
    width: 320px;
  }
  .header__top, .header__nav {
    padding: 12px;
  }
  .header__nav.active {
    margin-bottom: 220px;
  }
  .logo {
    height: 20px;
    margin-right: 15px;
  }
  .logo img {
    width: 120px;
  }
  .search__input {
    display: none;
  }
  .options {
    display: none;
  }
  .user-block__name--hide {
    display: none;
  }
  .nav {
    justify-content: flex-end;
    padding-right: 0;
  }
  .nav__toggler {
    display: block;
  }
  .nav__list {
    display: none;
    position: absolute;
    top: 105px;
    width: 296px;
    flex-direction: column;
  }
  .nav__item {
    justify-content: center;
  }
  .nav__list.show {
    display: block;
  }
  .main__layout {
    flex-direction: column;
  }
  .main__item {
    width: 320px;
  }
  .coments__item {
    width: 320px;
  }
  .support__list {
    width: auto;
    flex-direction: column;
  }
  .support__item {
    width: auto;
  }
}
